/* eslint-disable no-undef,no-unused-vars,jsx-a11y/no-static-element-interactions */
import React from 'react';
import Head from 'next/head';
import Router from 'next/router';
import Layout from '../comps/BlogLayout';
import P from '../comps/P';
import InlineCode from '../comps/InlineCode';
// import AppBar from '../comps/AppBar';

export default function WithDoc(options) {
  const handler = () =>
    Router.push(`/blog/${options.slug}`).then(() => window.scrollTo(0, 0));
  return function(content) {
    return class BlogPost extends React.Component {
      render() {
        return (
          <Layout>
            <Head>
              <title>{options.title}</title>
              <meta name="description" content={options.description} />
            </Head>
            {/*<AppBar activePageName="Blog" />*/}
            <main className="outerContainer">
              <div className="headerTop">
                <a href="/blog">Go back</a>
              </div>
              <div className="innerContainer">
                <div className="content">
                  <div className="date">Published on {options.date}</div>
                  <h1>{options.title}</h1>
                  <div className="markdown-content">{content}</div>
                </div>
              </div>
              <div
                style={{
                  position: 'absolute',
                  paddingRight: 20,
                  paddingBottom: 60,
                }}
              >
                <button onClick={handler}>Top</button>
              </div>
              <div className="footerWrapper">
                <div className="copyright">&copy; 2018, Pharrell.zx WANG</div>
              </div>
            </main>
            <style jsx>{`
              .backIcon {
                padding-right: 6px;
              }

              a {
                color: #424242;
                text-decoration: underline;
                opacity: 0.6;
              }

              a:hover {
                color: #212121;
                opacity: 1;
              }

              .footerWrapper {
                margin-top: 72px;
                border-top: 1px solid #666;
                backgroundcolor: #fff;
                height: 120px;
                z-index: 1100;
              }
              .copyright {
                flex: 1;
                // margin-top: 36;
                padding-top: 36px;
                text-align: center;
              }
              .headerTop {
                flex: 1;
                padding-top: 12px;
                padding-left: 30px;
                padding-right: 30px;
                padding-bottom: 12px;
                font-size: 0.5 rem;
                font-weight: 400;
                font-family: Roboto, Helvetica, Arial, sans-serif;
                line-height: 1.35417em;
                color: rgba(0, 0, 0, 0.87);
                background-color: #fff;
                position: fixed;
                top: 0px;
                z-index: 200px;
                width: 100%;
                // padding: 10px;
                // border: 1px solid #77aaff;
              }
              .outerContainer {
                display: flex;
                min-height: 100vh;
                flex-direction: column;
                background-color: #fafafa;
                width: 100%;
                height: 100%;
                // padding-top: 56;
              }

              .innerContainer {
                padding: 0 30px;
                flex: 1;
              }

              .content {
                margin: 0 auto 0 auto;
                max-width: 650px;
                padding-top: 36px;
              }

              .content h1 {
                color: #333;
                font-size: 30px;
                margin: 0;
              }

              .date {
                color: #888;
                font-size: 13px;
                padding-top: 48px;
              }

              .markdown-content {
                margin: 50px 0 0 0;
              }
            `}</style>

            <style jsx global>{`
              .markdown-content {
                color: #333;
                line-height: 28px;
                font-size: 18px;
              }

              .markdown-content hr {
                border: 0;
                border-bottom: 1px solid #8bc34a;
                margin: 40px 60px;
              }

              .markdown-content a {
                color: #1e88e5;
                text-decoration: none;
                border-bottom: 1px solid #1e88e5;
              }

              .markdown-content li {
                margin: 10px 0;
              }

              .markdown-content h2 {
                font-size: 25px;
                margin: 40px 0 10px 0;
                line-height: 30px;
              }

              .markdown-content h3 {
                font-size: 18px;
                margin: 30px 0 0 0;
                text-transform: uppercase;
                line-height: 22px;
                letter-spacing: 0.5px;
              }

              .markdown-content blockquote {
                padding: 1px 0 1px 15px;
                margin: 0;
                border-left: 5px solid #8bc34a;
              }
            `}</style>
          </Layout>
        );
      }
    };
  };
}

export const components = {
  p: P,
  code: InlineCode,
};



